@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  display: flex;
  margin: 0;
  min-height: 100%;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #1c1b21;
}
.page__main {
  flex-grow: 1;
}
@media (min-width: 1110px) {
  .page {
    background-image: url("../img/hero-bg@2x.jpg");
  }
}
.document {
  height: 100%;
}
.header {
  padding: 30px 0;
  margin-bottom: 320px;
}
.navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.navigation__logo {
  display: block;
  margin-right: 65px;
}
.navigation__link {
  font-size: 18px;
  line-height: 27px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
}
.navigation__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 420px) {
  .navigation__list {
    padding: 2em 2em 0 2em;
    row-gap: 2em;
  }
}
.navigation__item {
  margin-right: 56px;
}
@media (max-width: 420px) {
  .navigation__item {
    margin-right: 2em;
  }
}
.hero {
  padding-bottom: 130px;
}
.hero__container {
  display: flex;
  max-width: 1110px;
  margin: 0 auto;
  flex-direction: column;
  text-transform: capitalize;
}
@media (max-width: 420px) {
  .hero__container {
    padding: 0 1em;
  }
}
.hero__pretitle {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #ffffff;
}
.hero__single-name {
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: transparent;
  -webkit-text-stroke-color: #ffffff;
  -webkit-text-stroke-width: 1px;
}
.hero__audio {
  width: 100%;
}
.logo__icon {
  display: block;
}
.news {
  padding-bottom: 100px;
}
.news-list {
  display: flex;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  gap: 30px;
}
.news-item__wrapper {
  max-width: 350px;
  position: relative;
}
.news-item__image {
  display: block;
  border-radius: 20px 0;
}
@media (max-width: 420px) {
  .news-item__image {
    max-width: 320px;
    height: auto;
  }
}
.news-item__text {
  position: absolute;
  max-width: calc(350px - 1em);
  width: 100%;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 1em;
  padding-right: 0;
  border-radius: 20px 0;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
  background-color: rgba(122, 102, 204, 0.7);
}
@media (max-width: 420px) {
  .news-item__text {
    max-width: calc(320px - 1em);
  }
}
.news-item__text--uppercase {
  position: static;
  background-color: transparent;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}
